<template>
  <div class="day7-forecast">
    <ul>
      <li v-for="(day, index) in days" :key="index">
        <span class="wdate">{{wdays[index]}}<div class="split">|</div>
          {{days[index].date.substr(5, 5).replace("-", "/")}}</span>
                  <span class="wicon">
          <img :src="require(`../../assets/img/wicon/${wcode[index]}.png`)" />
        </span>
        <span class="wtemp">{{days[index].tmp_min}}° - {{days[index].tmp_max}}°</span>

      </li>
    </ul>
  </div>
</template>

<script>
  import {
    getForecastDate
  } from "network/api";

  export default {
    data() {
      return {
        days: [],
        wdays: ['今天', '明天'],
        wcode: []
      };
    },
    created() {
      getForecastDate().then(res => {

        this.days = res.data.HeWeather6[0].daily_forecast;

        var weekDay = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
        for (var i = 2; i < this.days.length; i++) {
          this.wdays.push(weekDay[new Date(this.days[i].date).getDay()]);
        }

        for (var i = 0; i < this.days.length; i++) {
          this.wcode.push(this.days[i].cond_code_d);
        }

      })
    }
  };
</script>

<style>
  .day7-forecast {
    width: 340px;
    height: 300px;

    margin: 0 auto;
    margin-top: -40px;
    margin-bottom: 30px;
    padding: 0 26px;
