    box-sizing: border-box;
    /* border: 3px solid rgb(29, 29, 29); */
    border-radius: 20px;
    box-shadow: 0px 0px 10px rgba(100, 100, 100, 0.3),
    inset 0px 0px 10px rgba(100, 100, 100, 0.3);
    /* background-color: rgba(213, 227, 255, .9); */
    /* 235, 241, 255 */
        box-shadow: 3px 1px 12px rgba(100, 100, 100, .1),
      inset 0px 1px 10px rgba(100, 100, 100, .4);
    /* opacity: 10%; */
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;

  }

  ul li {
    flex: 1;
    height: 30px;
    width: 100%;
    /* width: 250px; */
    margin: 8px 0;
    /* border: var(--bdBox); */

    display: flex;
    align-items: center;
  }

  ul li:not(:first-child){
    color: rgba(0, 0, 0, 0.555);
  }

  ul li span {
    font-family: 'Alibaba-PuHuiTi-Medium', 'Futura-LT-Condensed-Medium', 'Futura-LT-Bold';
    font-size: 18px;
    /* font-weight: 700; */
    /* border: var(--bdBox); */
  }

  ul li .wdate {
    /* flex: 1; */
    /* width: 120px; */
