  }
  .split {
    display: inline-block;
    width: 20px;
    text-align: center;
    /* font-weight: 700; */
    font-family: 'Alibaba-PuHuiTi-Light';
  }

  ul li .wicon {
    flex: 1;
    text-align: center;
    /* width: 58px; */
    /* height: 18px; */
    /* overflow: hidden; */
    /* display: table-cell; */
    /* vertical-align: middle; */
  }

  ul li .wicon img {
    width: 22px;
