<template>
  <div class="main-board">
    <div class="weather-board">
      <div class="local">
        <img src="" />
        <div class="district">{{district}}</div>
      </div>
      <div class="nowtmp">{{nowtmp}}
        <div class="degree-symbol">°</div>
      </div>
      <div class="weather-big-icon">
        <img :src="require(`../../assets/img/wicon/${iconcode}.png`)" />
      </div>
      <div class="winfo">{{winfo}}</div>
    </div>
    
  </div>
</template>

<script>
  import {
    getNowDate
  } from "network/api";
  export default {
    data() {
      return {
        district: null,
        nowtmp: null,
        // 引用本地图片，data中必须用require加载，否则会当成字符串来处理，后期后台传过来时需要修改
        // imgurl: 'require("@/assets/img/mbe/1/qingtian@3x.png")',
        iconcode: '101',
        winfo: null,

      }
    },
    created() {
      getNowDate().then(res => {

        const resData = res.data.HeWeather6[0];

        this.nowtmp = resData.now.tmp;
        this.district = resData.basic.location.toUpperCase(); // 大写
        this.iconcode = resData.now.cond_code;
        this.winfo = resData.now.cond_txt;
      })
    }

  }
</script>

<style>
  .main-board {
    height: 400px;
    /* box-shadow: 0 1px 1px rgba(100, 100, 100, .3); */

    /* background: #9CECFB;
    background: -webkit-linear-gradient(to left, #196aec, #65C7F7, #9CECFB);
    background: linear-gradient(to left, #196aec, #65C7F7, #9CECFB); */

    /* background-color: rgba(20,121,255, .4); */

    display: flex;
    justify-content: center;

    /* position: relative; */

  }

  .weather-board {
    width: 200px;
    height: 300px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
  }

  .local {
    flex: 0.5;
    text-align: center;
  }

  .district {
    font-family: 'Alibaba-PuHuiTi-Medium', 'Futura-LT-Condensed-Medium', 'Futura-LT-Bold';
    font-size: 22px;
    /* font-weight: 700; */
  }

  .nowtmp {
    flex: 1;
    text-align: center;
    font-family: 'Alibaba-PuHuiTi-Medium';
    padding-top: 20px;
    font-weight: 700;
    font-size: 4.8em;

    position: relative;
  }

  .degree-symbol {
    position: absolute;
    top: 10px;
    right: 30px;
    font-size: 50px;
    font-weight: 400;
  }


  .weather-big-icon {
    flex: 1;
    text-align: center;
  }

  .weather-big-icon img {
    height: 100px;
    vertical-align: top;
  }

  .winfo {
    text-align: center;
    font-family: 'Alibaba-PuHuiTi-Medium';
    /* font-size: 16px; */
    /* font-weight: 700; */
  }


</style>